  background-size: cover;
  display: -ms-flexbox;
  display: flex;
  height: 100vh;
  -ms-flex-pack: center;
      justify-content: center;
  width: 100vw;
}

.box-layout__box {
  background: rgba(244, 244, 244, 0.95);
  border-radius: 5px;
  text-align: center;
  width: 16rem;
  padding: 1rem 1rem 2.5rem 1rem;
}

.box-layout__title {
  line-height: 1;
}



